<script>
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Footer",
  data() {
    return {
      email: ''
    }
  },
  methods: {
    validateEmail(email) {
      return email === '' || String(email)
          .toLowerCase()
          .match(
              /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|.(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          ) ;
    },
    subscribe() {
      if (!this.validateEmail(this.email)) {
        return this.$alert('Wrog email', 'Error','error');
      }
      setTimeout(() => {
        this.$alert('You have successfully subscribed', 'Successfully send', 'success');
        this.email = ''
      })
    }
  }
}
</script>

